@import url("https://fonts.googleapis.com/css?family=Roboto:300,300i,400,400i,500");
@import url("https://fonts.googleapis.com/css?family=Yanone+Kaffeesatz:300,400,700");
body {
  background-color: #f5f8fa;
  box-sizing: border-box;
  height: 2000px;
  font-family: "Roboto", sans-serif;
}

.content {
  max-width: 1100px;
}

.main-page .main-hero {
  padding-top: 150px;
  padding-bottom: 20px;
  background: #51bf87;
  background: -webkit-linear-gradient(55deg, #51bf87 53%, #59d587 50%, #51bf87 100%);
  background: -o-linear-gradient(55deg, #51bf87 53%, #59d587 50%, #51bf87 100%);
  background: -moz-linear-gradient(55deg, #51bf87 53%, #59d587 50%, #51bf87 100%);
  background: linear-gradient(55deg, #51bf87 53%, #59d587 50%, #51bf87 100%);
}

.main-page .main-hero .header {
  margin: 0px;
  text-align: center;
  color: #ecc969;
  text-shadow: 0px 0px 19px #3d7a68;
  letter-spacing: 1.9px;
  font-size: 39px;
  font-weight: 400;
  font-family: 'Yanone Kaffeesatz', sans-serif;
  margin-bottom: 20px;
}

.main-page .main-hero .hero-img-container {
  -webkit-box-shadow: 0px 10px 69px -24px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 10px 69px -24px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 10px 69px -24px rgba(0, 0, 0, 0.75);
  max-width: 600px;
  margin: 0 auto;
  padding: 10px 20px;
}

@media (max-width: 767px) {
  .main-page .main-hero {
    padding-top: 120px;
    padding-bottom: 20px;
  }
  .main-page .main-hero .header {
    margin: 0px;
    text-align: center;
    color: #ecc969;
    text-shadow: 0px 0px 19px #3d7a68;
    letter-spacing: 1.9px;
    font-size: 39px;
    font-weight: 400;
    font-family: 'Yanone Kaffeesatz', sans-serif;
    margin-bottom: 20px;
  }
  .main-page .main-hero .hero-img {
    max-height: 350px;
    margin: 0 auto;
  }
}
